// GlobalStyle.tsx
import { createGlobalStyle } from 'styled-components';
import { darkSber } from '@sberdevices/plasma-tokens/themes'; // –ò–ª–∏ –æ–¥–∏–Ω –∏–∑ —Å–ø–∏—Å–∫–∞: darkEva, darkJoy, lightEva, lightJoy, lightSber
import {
    text, // –¶–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞
    background, // –¶–≤–µ—Ç –ø–æ–¥–ª–æ–∂–∫–∏
    gradient, // –ì—Ä–∞–¥–∏–µ–Ω—Ç
} from '@sberdevices/plasma-tokens';

const DocumentStyle = createGlobalStyle`
    html:root {
        min-height: 100vh;
        color: ${text};
        background-color: ${background};
        background-image: ${gradient};
    }
`;
const ThemeStyle = createGlobalStyle(darkSber);
export const GlobalStyle = () => (
    <>
        <DocumentStyle />
        <ThemeStyle />
    </>
);